@include exports("treeview/theme") {


    $treeview-item-border-width: 0;
    $treeview-item-border-radius: 0;
    $treeview-item-focused-shadow: $focused-shadow;


    // Appearance
    .k-treeview {

        // Link
        .k-in {
            @include border-radius( $treeview-item-border-radius );
            border-width: $treeview-item-border-width;
        }
        .k-in.k-state-hover {
            @include appearance( hovered-node );
        }
        .k-in.k-state-selected {
            @include appearance( selected-node );
        }
        .k-in.k-state-focused {
            box-shadow: $treeview-item-focused-shadow;
        }
        .k-in.k-state-disabled {}
    }

}
